* {
  font-family: Roboto;
}

header {
  height: 60px;
  color: #fff;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  display: flex;
}

h1 {
  font-size: 24px;
}

form {
  align-items: center;
  margin-left: 20px;
  display: flex;
}

input[type="text"] {
  height: 30px;
  width: 300px;
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
}

button[type="submit"] {
  height: 30px;
  width: 30px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  margin-left: 10px;
}

.buttons {
  justify-content: space-between;
  margin-right: 20px;
  display: flex;
}

button {
  height: 30px;
  width: 100px;
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  font-size: 16px;
}

button:hover {
  background-color: #3e8e41;
}

main {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

#movie-cards {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.movie-card {
  width: calc(33.33% - 1rem);
  cursor: pointer;
  transition: transform .2s ease-in-out;
}

.movie-card:hover {
  cursor: pointer;
  transform: translateY(-.5rem);
  box-shadow: 2px 2px 20px #00000080;
}

.movie-modal {
  max-width: 620px;
  max-height: 562px;
  text-align: center;
  background-color: #fff;
  border-radius: .5rem;
  grid-template-columns: 1fr 2fr;
  gap: 20px;
  margin: 0 auto;
  padding: 2rem;
  display: grid;
}

.modal-poster {
  width: 100%;
  height: auto;
  border-radius: .5rem;
}

.modal-title {
  font-size: 2rem;
  font-weight: bold;
}

.modal-info {
  color: #666;
  margin: .5rem 0 1rem;
  font-size: 1rem;
}

.modal-about {
  text-align: justify;
  margin: 1rem 0;
  font-size: 1.2rem;
  line-height: 1.5;
}

.modal-buttons {
  justify-content: center;
  margin-top: 2rem;
  display: flex;
}

.watch-btn, .queue-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: .5rem;
  margin: 0 .5rem;
  padding: .5rem 1rem;
  font-size: 1.2rem;
  font-weight: bold;
  transition: background-color .2s ease-in-out;
}

.watch-btn:hover, .queue-btn:hover {
  background-color: #0062cc;
}

#pagination {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.page-number {
  height: 30px;
  width: 30px;
  cursor: pointer;
  border: 1px solid #333;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  display: flex;
}

.page-number.active {
  color: #fff;
  background-color: #333;
  border: none;
}

